import { Component, Directive, Input } from '@angular/core';
import { NotificationOrigins } from '../../models/notification-origins';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NOTIFICAION_ORIGIN_ICONS } from '../../constants/notification-origin-icons';

@Component({
  selector: 'bb-notifications-capability-icon',
  templateUrl: './notifications-capability-icon.component.html',
})
export class NotificationsCapabilityIconComponent {
  /**
   * Notification origin taked from `NotificationOrigins`
   */
  @Input() origin?: NotificationOrigins | string;

  /**
   * Notification origin types
   */
  readonly originTypes = NotificationOrigins;

  /**
   * Notification origin icons
   */
  readonly originIcons = NOTIFICAION_ORIGIN_ICONS;
}

@Directive({
  selector: '[bbNotificationsCapabilityIconCustomizable]',
})
export class NotificationsCapabilityIconCustomizableDirective extends BbTemplate<
  NotificationsCapabilityIconComponent,
  { origin: NotificationOrigins | string }
> {}
